import Dexterity from '../Types/DexteritySkillType';
import Signs from '../Types/SignsSkillType';
import Strength from '../Types/StrengthSkillType';
import Mind from '../Types/MindSkill';

const polishDictionary = new Map([
  [Strength.Strength, 'Sia'],
  [Strength.HeavyWeapon, 'Bro cika'],
  [Strength.HeavyArmor, 'Ciki pancerz'],
  [Strength.FistFight, 'Walka na pici'],
  [Strength.Wholesomness, 'Zdrowotno'],
  [Strength.Endurance, 'Wytrzymao'],
  [Strength.ElixirTolerance, 'Tolerancja eliksirw'],
  [Strength.Bullying, 'Zastraszanie'],
  [Dexterity.Dexterity, 'Zrczno'],
  [Dexterity.LightWeapon, 'Bro lekka'],
  [Dexterity.LightArmor, 'Lekki pancerz'],
  [Dexterity.Initiative, 'Inicjatywa'],
  [Dexterity.Stealth, 'Skradanie si'],
  [Dexterity.Dodge, 'Uniki'],
  [Dexterity.ThrowingWeapon, 'Bro miotana'],
  [Dexterity.RangeWeapon, 'Bro dystansowa'],
  [Signs.Signs, 'Znaki'],
  [Signs.Somne, 'Somne'],
  [Signs.Heliotrop, 'Heliotrop'],
  [Signs.Aard, 'Aard'],
  [Signs.Igni, 'Igni'],
  [Signs.Axii, 'Axii'],
  [Signs.Yrden, 'Yrden'],
  [Signs.Quen, 'Quen'],
  [Mind.Mind, 'Umys'],
  [Mind.Crafting, 'Wytwarzanie'],
  [Mind.MonsterKnowledge, 'Wiedza o potworach'],
  [Mind.WorldKnowledge, 'Wiedza o wiecie'],
  [Mind.Rhetoric, 'Retoryka'],
  [Mind.WitcherSenses, 'Wiedmiskie zmysy'],
  [Mind.MagicResists, 'Odporno magiczna'],
  [Mind.Arcana, 'Arkana'],
]);

/**
 * Return translation in polish language
 * @param {Strength | Dexterity | Signs | Mind} key
 * @returns {String}
 */
function takeTranslate(key) {
  return polishDictionary?.get(key) || key;
}

export { takeTranslate as default };
